import { util } from "./../index";
export class Loading {
  constructor(isloading) {}

  /**  */
  show(msg) {
    if (this.isloading) this.hide();
    msg = msg || "...";
    util.f7.f7App.dialog.preloader(msg);
    this.isloading = true;
  }

  /**  */
  hide() {
    util.f7.f7App.dialog.close();
    this.isloading = false;
  }
}



