import Vue from "vue";
import { util } from "./../index";

/**  */
export class HttpClient {
  /** */
  constructor() {
    this.init();
  }

  /**  */
  init() {
    Vue.http.interceptors.push((request, next) => {
      let timeout;
      //  _timeout
      if (request._timeout) {
        timeout = setTimeout(() => {
          // status:408,statustext:""next
          next(
            request.respondWith(request.body, {
              status: 408,
              statusText: ""
            })
          );
        }, request._timeout);
      }
      next(response => {
        console.log(response.status); //408
        return response;
      });
    });
  }

  /**
   * url 
   */
  urlEncode(param, key, encode) {
    if (param == null) return "";
    var paramStr = "";
    var t = typeof param;
    if (t == "string" || t == "number" || t == "boolean") {
      paramStr +=
        "&" +
        key +
        "=" +
        (encode == null || encode ? encodeURIComponent(param) : param);
    } else {
      for (var i in param) {
        var k =
          key == null
            ? i
            : key + (param instanceof Array ? "[" + i + "]" : "." + i);
        paramStr += this.urlEncode(param[i], k, encode);
      }
    }
    return paramStr;
  }

  //
  utilHttpHandle(pos) {
    pos.data = pos.data || {};
    pos.headers = pos.headers || {};
    pos.headers["Content-Type"] = "application/json";
    util.globalHeader.merge(pos.headers); //
    pos._timeout = 10000;
    return pos;
  }

  /**  */
  get(pos) {
    pos = this.utilHttpHandle(pos);
    pos.method = "GET";
    this.send(pos);
  }

  /**  */
  post(pos) {
    pos = this.utilHttpHandle(pos);
    pos.method = "POST";
    this.send(pos);
  }

  /**  */
  put(pos) {
    pos = this.utilHttpHandle(pos);
    pos.method = "PUT";
    this.send(pos);
  }

  /**  */
  delete(pos) {
    pos = this.utilHttpHandle(pos);
    pos.method = "DELETE";
    this.send(pos);
  }

  /**  */
  send(pos) {
    switch (pos.method) {
      case "GET":
        pos.params = pos.data;
        Vue.http.get(pos.url, pos).then(pos.success, pos.error);
        break;
      case "POST":
        Vue.http.post(pos.url, pos.data, pos).then(pos.success, pos.error);
        break;
      case "PUT":
        Vue.http.put(pos.url, pos.data, pos).then(pos.success, pos.error);
        break;
      case "DELETE":
        pos.params = pos.data;
        Vue.http.delete(pos.url, pos).then(pos.success, pos.error);
        break;
    }
  }
}



