export class GlobalHeader {
  /**  */
  constructor() {
    this._headers = [];
  }

  /** */
  add(key, value) {
    for (const item of this._headers) {
      if (item.key === key) {
        item.value = value;
        return;
      }
    }
    this._headers.push({
      key: key,
      value: value
    });
  }

  /**  */
  remove(key) {
    for (const item of this._headers) {
      if (item.key === key) {
        var index = this._headers.indexOf(item);
        this._headers.splice(index, 1);
        return;
      }
    }
  }

  /**  */
  merge(headers) {
    if (!headers) return;
    for (const item of this._headers) {
      headers[item.key] = item.value;
    }
  }
}



