<template>
  <img
    @click="clickHandle"
    v-lazy:background-image=" src ? ( src + zoomUrl) : ''"
    :key="src"
    :width="width"
    :height="height"
    :class="bindClass + ' replace-img' + (circular ?  ' circular' : '')"
    :style="{
        'background-size':modeStyle,
        'border-radius':radius
    }"
    :src="replaceSrc"
  />
</template>

<script>
export default {
  props: {
    /****/
    src: {
      default() {
        return "";
      }
    },
    /**  1:2 **/
    size: {
      default() {
        return "";
      }
    },
    /****/
    width: {
      default() {
        return "100%";
      }
    },
    /****/
    height: {
      default() {
        return "auto";
      }
    },
    /** 360 **/
    zoom: {
      default() {
        return "";
      }
    },
    /****/
    bindClass: {
      default() {
        return "";
      }
    },
    /****/
    circular: {
      default() {
        return false;
      }
    },
    /**  */
    radius: {
      default() {
        return "";
      }
    },
    /**
     * 
     * scaleToFill  image 
     * aspectFit  
     * aspectFill 
     * **/
    mode: {
      default() {
        return "auto";
      }
    },
    /**  () */
    preview: {
      default() {
        return false;
      }
    }
  },
  computed: {
    /**  */
    zoomUrl() {
      if (!this.zoom) return "";
      return `?imageView2/2/w/${this.zoom}/h/${this.zoom}`;
    },
    /**  */
    modeStyle() {
      switch (this.mode) {
        case "scaleToFill":
          return "100% 100%";
        case "aspectFit":
          return "contain";
        case "aspectFill":
          return "cover";
      }
      return this.mode;
    },
    /**  */
    replaceSrc() {
      if (!this.size) return "static/replace/rectangle_2_1_replace.png";
      var scaleParam = this.size.split(":");
      return `static/replace/rectangle_${scaleParam[0]}_${
        scaleParam[1]
      }_replace.png`;
    }
  },
  methods: {
    clickHandle(e) {
      if (this.preview) {
        var imagePreview = this.$util.f7.f7App.photoBrowser.create({
          photos: [this.src],
          swipeToClose: false,
          theme: "dark"
        });
        imagePreview.open();
        return;
      }
      this.$emit("click", e);
    }
  }
};
</script>

<style scoped>
.replace-img {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}
.circular {
  border-radius: 50%;
}
</style>




