export class QueryModel {
  constructor(
    page,
    pageCount,
    pageSize,
    totalCount,
    order,
    hasMore,
    isPulldown,
    loading,
    keyword
  ) {
    this.isPulldown = false;
    this.clear();
    this.page = 1;
    this.pageSize = 10;
    this.loading = false;
    this.firstLoad = true;
    this.keyword = "";
  }

  /**
   *  
   * @param {*} pagerModel
   */
  extends(pagerModel) {
    this.firstLoad = false;
    this.page = pagerModel.page;
    this.pageCount = pagerModel.pageCount;
    this.pageSize = pagerModel.pageSize;
    this.totalCount = pagerModel.totalCount;
    this.order = pagerModel.order;
    this.hasMore = this.isHasMore();
    this.loading = false;
    this.isPulldown = false;
  }

  /**
   * 
   */
  noMore() {
    this.hasMore = false;
  }

  /**
   * 
   */
  isHasMore() {
    return this.page < this.pageCount;
  }

  /**
   *  
   */
  get pagePreloader() {
    if (this.isPulldown) return false;
    if (this.loading) return true;
    if (this.isNoData) return false;
    return this.hasMore;
  }

  /**
   * 
   */
  get canLoadmore() {
    if (this.loading) return false;
    return this.hasMore;
  }

  /**
   * 
   */
  get isNoData() {
    if (this.loading) return false;
    if (this.firstLoad) return false;
    return this.totalCount === 0;
  }

  /**
   * 
   */
  clear() {
    this.page = 1;
    this.pageCount = 0;
    this.totalCount = 0;
    this.hasMore = true;
  }

  /**
   * 
   */
  nextPage() {
    this.page += 1;
    this.loading = true;
  }

  /**
   * 
   */
  needPageRequest(itemIndex) {
    if (this.page * this.pageSize > itemIndex) return 0;
    var needPage = 0;
    while (true) {
      if ((this.page + needPage) * this.pageSize > itemIndex) break;
      needPage++;
    }
    return needPage;
  }

  /**
   * 
   */
  pullDown() {
    this.isPulldown = true;
    this.loading = true;
  }
}



