import { util } from "./../index";

export class Message {
  /**  */
  alert(msg) {
    util.f7.f7App.dialog.alert(msg);
  }

  /**  */
  toast(msg, callback) {
    util.f7.f7App.toast
      .create({
        text: msg,
        position: "center",
        closeTimeout: 2000,
        on: {
          closed: callback
        }
      })
      .open();
  }

  /**  */
  confirm(msg, callBack, noCallBack) {
    util.f7.f7App.dialog.confirm(msg, "", callBack, noCallBack);
  }

  /**  */
  prompt(title, msg, callBack, noCallBack, defaultValue) {
    util.f7.f7App.dialog.prompt(msg, title, callBack, noCallBack, defaultValue);
  }
}



