(function (w) {
    // 
    function shield() {
        return false;
    }
    document.addEventListener('touchstart', shield, false); //active
    document.oncontextmenu = shield; //
    // H5 plus
    var ws = null,
        as = 'pop-in';

    function plusReady() {
        ws = plus.webview.currentWebview();
        compatibleAdjust();
    }
    if (w.plus) {
        plusReady();
    } else {
        document.addEventListener('plusready', plusReady, false);
    }
    // DOMContentLoaded
    var domready = false;
    document.addEventListener('DOMContentLoaded', function () {
        domready = true;
        gInit();
        document.body.onselectstart = shield;
        compatibleAdjust();
    }, false);
    // 
    w.back = function (hide) {
        if (w.plus) {
            ws || (ws = plus.webview.currentWebview());
            if (hide || ws.preate) {
                ws.hide('auto');
            } else {
                ws.close('auto');
            }
        } else if (history.length > 1) {
            history.back();
        } else {
            w.close();
        }
    };
    // 
    var openw = null,
        waiting = null;
    /**
     * 
     * @param {URIString} id : url
     * @param {boolean} wa : 
     * @param {boolean} ns : 
     * @param {JSON} ws : Webview
     */
    w.clicked = function (id, wa, ns, ws) {
        if (openw) { //
            return null;
        }
        if (w.plus) {
            wa && (waiting = plus.nativeUI.showWaiting());
            ws = ws || {};
            ws.scrollIndicator || (ws.scrollIndicator = 'none');
            ws.scalable || (ws.scalable = false);
            var pre = ''; //'http://192.168.1.178:8080/h5/';
            openw = plus.webview.create(pre + id, id, ws);
            ns || openw.addEventListener('loaded', function () { //
                //		setTimeout(function(){//
                openw.show(as);
                closeWaiting();
                //		},200);
            }, false);
            openw.addEventListener('close', function () { //
                openw = null;
            }, false);
            return openw;
        } else {
            w.open(id);
        }
        return null;
    };
    w.openDoc = function (t, c) {
        var d = plus.webview.getWebviewById('document');
        if (d) {
            d.evalJS('updateDoc("' + t + '","' + c + '")');
        } else {
            d = plus.webview.create('/plus/doc.html', 'document', {
                zindex: 9999,
                popGesture: 'hide'
            }, {
                    preate: true
                });
            d.addEventListener('loaded', function () {
                d.evalJS('updateDoc("' + t + '","' + c + '")');
            }, false);
        }
    }
    /**
     * 
     */
    w.closeWaiting = function () {
        waiting && waiting.close();
        waiting = null;
    }
    // 
    var adjust = false;

    function compatibleAdjust() {
        if (adjust || !w.plus || !domready) {
            return;
        } // iOSdiv
        if ('iOS' == plus.os.name) {
            var t = document.getElementById("dcontent");
            t && (t.className = "sdcontent");
            t = document.getElementById("content");
            t && (t.className = "scontent");
            //iOS8div
            var lasto = window.orientation;
            window.addEventListener("orientationchange", function () {
                var nowo = window.orientation;
                if (lasto != nowo && (90 == nowo || -90 == nowo)) {
                    window.dcontent && (0 == dcontent.scrollTop) && (dcontent.scrollTop = 1);
                    window.content && (0 == content.scrollTop) && (content.scrollTop = 1);
                }
                lasto = nowo;
            }, false);
        }
        adjust = true;
    };
    w.compatibleConfirm = function () {
        plus.nativeUI.confirm('OSmuiHello mui', function (e) {
            if (0 == e.index) {
                plus.runtime.openURL("http://www.dcloud.io/hellomui/");
            }
        }, "", ["", ""]);
    }
    // 
    var _dout_ = null,
        _dcontent_ = null;
    w.gInit = function () {
        _dout_ = document.getElementById("output");
        _dcontent_ = document.getElementById("dcontent");
    };
    // 
    w.outClean = function () {
        _dout_.innerText = "";
        _dout_.scrollTop = 0; //iOS8
    };
    // 
    w.outSet = function (s) {
        _dout_.innerText = s + "\n";
        (0 == _dout_.scrollTop) && (_dout_.scrollTop = 1); //iOS8
    };
    // 
    w.outLine = function (s) {
        _dout_.innerText += s + "\n";
        (0 == _dout_.scrollTop) && (_dout_.scrollTop = 1); //iOS8
    };
    // "HH:MM:SS"
    w.timeToStr = function (ts) {
        if (isNaN(ts)) {
            return "--:--:--";
        }
        var h = parseInt(ts / 3600);
        var m = parseInt((ts % 3600) / 60);
        var s = parseInt(ts % 60);
        return (ultZeroize(h) + ":" + ultZeroize(m) + ":" + ultZeroize(s));
    };
    // "YYYY-MM-DD HH:MM:SS"
    w.dateToStr = function (d) {
        return (d.getFullYear() + "-" + ultZeroize(d.getMonth() + 1) + "-" + ultZeroize(d.getDate()) + " " + ultZeroize(d.getHours()) + ":" + ultZeroize(d.getMinutes()) + ":" + ultZeroize(d.getSeconds()));
    };
    /**
     * zeroize value with length(default is 2).
     * @param {Object} v
     * @param {Number} l
     * @return {String} 
     */
    w.ultZeroize = function (v, l) {
        var z = "";
        l = l || 2;
        v = String(v);
        for (var i = 0; i < l - v.length; i++) {
            z += "0";
        }
        return z + v;
    };
})(window);


