<template>
  <f7-page>
    <util-nav title=""></util-nav>
    <f7-page-content>
      <f7-list no-hairlines-md>
        <f7-list-input
          label=""
          type="text"
          placeholder="11"
          clear-button
          :value="model.phoneNumber"
          @input="model.phoneNumber = $event.target.value"
        ></f7-list-input>

        <f7-list-input
          label=""
          type="password"
          placeholder="6"
          clear-button
          :value="model.token"
          @input="model.token = $event.target.value"
        ></f7-list-input>

      </f7-list>
      <f7-block>
        <f7-row>
          <f7-col>
            <f7-button
              @click="sendSms"
              fill
            ></f7-button>
          </f7-col>
          <f7-col>
            <f7-button
              @click="login"
              fill
            ></f7-button>
          </f7-col>
        </f7-row>
      </f7-block>
      <f7-block>
        <f7-row>
          <f7-col>
            <f7-button
              fill
              round
              @click="sendGet"
            ></f7-button>
          </f7-col>
        </f7-row>
      </f7-block>
      {{datas}}
    </f7-page-content>
  </f7-page>
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      datas: undefined,
      model: {
        phoneNumber: "",
        token: "",
        application: "inhome-app"
      }
    };
  },
  async mounted() {
    var result = await this.autoLoginAsync();
  },
  methods: {
    ...mapActions("user", [
      "sendLoginOrRegisterTokenAsync",
      "loginAsync",
      "autoLoginAsync"
    ]),
    /**  */
    async sendGet() {
      var datas = await this.autoLoginAsync();
      if (!datas) return;
      this.datas = datas;
    },
    /**  */
    async sendSms() {
      var result = await this.sendLoginOrRegisterTokenAsync(this.model);
      debugger;
    },
    /**  */
    async login() {
      var result = await this.loginAsync(this.model);
      if (!result) return;
      var datas = await this.autoLoginAsync(result);
    }
  }
};
</script>

<style>
</style>


